import React from 'react'
import { Link } from 'react-router-dom'
import Button from '@material-ui/core/Button'

export default class Nav extends React.Component {
    render() {    
        return (
            <>
            <div className="Nav_container">
                <h3 className="menu-headline">TruFlow </h3>
                <div className="Nav_left" >
                    <Button component={Link} to="/traveler/truflow/limitall">TruFlow List</Button>
                </div>
                <div className="Nav_left" >
                    <Button component={Link} to="/truflow/config">Configurator</Button>
                </div>
                <h3 className="menu-headline">MFC Calibration</h3>
                <div className="Nav_left" >
                    <Button component={Link} to="/mfccal/all">CDM List</Button>
                    <Button component={Link} to="/cert/equipment/all">Cal Equipment List</Button>
                </div>
                <h3 className="menu-headline">Work Order</h3>
                <div className="Nav_left" >
                    <Button component={Link} to="/workorder/all">Work Order List</Button>
                    <Button component={Link} to="/workorder/new">New Work Order</Button>
                    <Button component={Link} to="/customer/new">New Customer</Button>
                </div>
                <h3 className="menu-headline">eTraveler</h3>
                {/* <div className="Nav_left" >
                    <Button component={Link} to="/traveler/agitator/new" >New Agitator</Button>
                    <Button component={Link} to="/traveler/agitator/all">List All</Button>
                </div> */}
                <h3 className="menu-headline">eSHIPPING</h3>
                <div className="Nav_left" >
                    <Button component={Link} to="/shipping/all">Shipping List</Button>
                    <Button component={Link} to="/shipping/new">New Shipment</Button>
                </div>
            </div>
            </>
        );
    }
}

